.wrapper-orange {
    background: url('../img/bgor.jpg');
    background-repeat: no-repeat;
    background-size: cover;


    .logo-letter {
        color: #f8625c;
    }

    .checkbox-tools:checked + label {
        box-shadow: 0px 0px 15px 4px #F9625A;

        .label-title {
            background: #FFECBE;
        }

        .label-summ {
            color: #F8625C;
            ;
        }
    }
}


.btn-orange {
    background: linear-gradient(104.87deg, #F96957 28.29%, #FCD263 93.86%);

    &:hover {
        background: linear-gradient(240.87deg, #F96957 28.29%, #FCD263 93.86%);
    }
}
